.body .container {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1500px;
  margin: 40px 0;
  margin-left: -10px;
}

.backgroundImage {
    opacity: 0.7;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.body .container .card {
  position: relative;
  min-width: 296px;
  height: 260px;
  /* box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
    inset -5px -5px 15px rgba(255, 255, 255, 0.1),
    5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1); */
  border-radius: 15px;
  margin: -10px;
  transition: 0.5s;
}

.body .container .card:nth-child(1) .box .content a {
  background: #2196f3;
}

.body .container .card:nth-child(2) .box .content a {
  background: #e91e63;
}

.body .container .card:nth-child(3) .box .content a {
  background: #23c186;
}

.body .container .card .box {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: #2a2b2f;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.5s;
}

.body .container .card .box:hover {
  transform: translateY(-30px);
}

.body .container .card .box:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  /* background: rgba(255, 255, 255, 0.03); */
}

.body .container .card .box .content {
  padding: 20px;
  text-align: center;
}

.body .container .card .box .content h2 {
  position: absolute;
  top: -10px;
  right: 30px;
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.1);
}

.body .container .card .box .content h3 {
    position: relative;
  font-size: 1rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-top: 35px;
  /* margin-bottom: 15px; */
}

.body .container .card .box .content p {
  font-size: 1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}

.body .container .card .box .content a {
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: black;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.body .container .card .box .content a:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}
