@font-face {
  font-family: "Avenir LT Pro";
  src: local("Avenir LT Pro"),url("./Fonts/avenir-lt/AvenirLTProBook.otf") format("truetype");
}

@font-face {
  font-family: "Avenir LT Pro Black";
  src: local("Avenir LT Pro Black"),url("./Fonts/avenir-lt/AvenirLTProBlack.otf") format("truetype");
}

body {
  margin: 0;
  font-family: "Avenir LT Pro","Product Sans","Poppins", sans-serif !important ;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
