.main {
  background-color: rgb(20, 19, 19);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}

.centerHeadings {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 70px;
}

.heading {
  margin-top: 25px;
  margin-bottom: 20px;
  font-family: "Product Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 70px;
  line-height: 80px;
  text-align: center;
  background: -webkit-linear-gradient(89.56deg, #00c3cf 2.36%, #6f4bff 99.24%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#subHeading {
  font-family: "Product Sans";
  color: #fff;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 30px;
  /* identical to box height, or 150% */
  opacity: 0.7;
}

.logo {
  width: 70px;
  height: 70px;
}

.headingOfContent {
  font-family: "Product Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  color: #ffffff;
  margin-top: 2%;
}

.Maincontent {
  font-family: "Product Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  /* or 150% */
  color: #ffffff;
  opacity: 0.7;
}

.numbers {
  font-family: "Product Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  color: #ffffff;
}

.vl {
  border-left: 2px solid rgb(255, 255, 255);
  height: 70px;
  opacity: 0.7;
}

.profile {
  font-family: "Product Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  /* or 150% */
  color: #ffffff;
  opacity: 0.7;
}

.content {
  display: flex;
  flex-direction: row;
}

.story {
  display: flex;
  flex-direction: column;
  margin:5%;
}

.records {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 5%;
  margin-bottom: 5%;
}

.r1 {
  display: flex;
  flex-direction: column;
}

.supportUs {
  margin-top: 10px;
  width: 172px;
  height: 52px;
  font-weight: 700;
  background: #18191a;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  background: -webkit-linear-gradient(89.56deg, #00c3cf 2.36%, #6f4bff 99.24%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: pointer;
}
